<template>
  <div class="login-wrap">
    <el-form
      status-icon
      :model="loginForm"
      ref="loginForm"
      class="login-div"
      :rules="loginRule">
      <img src="@/assets/oola-mini.jpg" alt="" class="user-icon">
      <el-form-item
        prop="account">
        <el-input
          maxlength="50"
          placeholder="请输入账号"
          v-model.trim="loginForm.account"
          @keyup.native.enter="submitForm('loginForm')">
        </el-input>
      </el-form-item>
      <el-form-item
        prop="password">
        <el-input
          placeholder="请输入您的密码"
          type="password"
          maxlength="20"
          v-model.trim="loginForm.password"
          @keyup.native.enter="submitForm('loginForm')">
        </el-input>
      </el-form-item>
      <el-button @click="submitForm('loginForm')">确认登录</el-button>
      <!--<router-link :to="{name: 'register'}" class="register">没有账号？马上注册</router-link>-->
    </el-form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 表单
      loginForm: {
        account: '',
        password: ''
      },
      // 验证规则
      loginRule: {
        account: [
          { required: true, message: '请输入账号', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ]
      }
    }
  },
  mounted () {},
  methods: {
    submitForm (formName) {
      const self = this
      self.$refs[formName].validate((valid) => {
        if (valid) {
          self.$store.dispatch('loginByAdmin', self.loginForm).then((res) => {
            self.$message.success('登录成功')
            self.$router.push({path: '/mba'})
          }).catch(err => {
            self.$message.error(err.msg)
          })
        } else {
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    }
  }
}
</script>

<style lang="less" scoped>
  .login-wrap {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    // background: linear-gradient(180deg, #00FF66, #2BB3D5, #B8DD22);
    .login-div {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 400px;
      padding: 0 20px;
      background-color: white;
      border-radius: 4px;
      .el-form-item {
        width: 300px;
        .el-input{
        }
      }
      .el-button {
        width: 200px;
        color: whitesmoke;
        font-size: 18px;
        background-image: linear-gradient(-135deg, #FFBA22 0%, #FF8E00 100%);
        border-radius: 8px;
      }
      .register {
        display: block;
        padding: 20px 0;
        color: cornflowerblue;
      }
    }
  }
</style>



